.container{
    height: 60%;
    padding: 20vh;
    display: flex;
    margin-left: auto; 
    flex-direction: column;
    width: 20vw;
    min-width: 17vw;
    background-color: #ECECEC;
}
.h2{
    display: block;
    font-size: 1.17em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-left: 8px;
    margin-right: 8px;
}
.btn-Submit{
    margin-top: 5%; 
    background-color: white;
  padding: 5px 20px;
  border-radius: 26px;
  transition: 0.2s;

  &:hover{
    background-color: #f1f1f1;
    -webkit-box-shadow: 0px 5px 10px -2px rgba(47,47,47,0.236); 
    box-shadow: 0px 4px 7px -2px rgba(47, 47, 47, 0.236);
  }
  &:active{
    background-color: #0E8388;
    color: #f1f1f1;
    -webkit-box-shadow: 0px 5px 10px -2px rgba(42, 42, 42, 0.36); 
    box-shadow: 0px 3px 10px -2px rgba(42, 42, 42, 0.36);
  }
}